.contenedor-skills {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, 1fr);

    height: 70%;
    width: 80%;
    margin: 0 auto;

    justify-content: center;
    align-content: center;

    @media (max-width: 855px) {
        width: 100%;
        overflow: hidden;
        margin-top: 10px;
        margin-left: 10px;
    }
}
