.cuadro {
    width: 95%;
    height: 50%;
    border-radius: 5px;
    border: 4px solid #707070;
    background-size: contain;
    filter: saturate(0.5);
    transition: all 300ms;

    Button {
        margin-top: 250px;
        margin-left: 120px;
        @media (max-width: 855px) {
            margin-top: 120px !important;
            margin-left: 70px;
        }
        @media (max-width: 500px) {
            margin-left: 160px;
        }
    }
    &:hover {
        filter: saturate(1);
    }
    @media (max-width: 855px) {
        height: 70%;
        background-size: cover;
        background-position: center;
    }
    @media (max-width: 500px) {
        height: 100%;
        width: 130%;
        margin-bottom: 30px;
    }
}
